<template>
	<div class="all"> 
		    <el-card :body-style="{ padding: '20px' }"  style="height: 600px;width:1200px; text-align: center;margin-left: 170px;">
				<el-button type="text" style="position: absolute;margin-left: 580px; font-size: 17px;" @click = "updateDialogVisible = true">修改信息</el-button>
					<img :src= "user[0].headimg" class="image">
					<div style="padding: 14px;">
					<span style="font-size: 25px;">{{ user[0].name }}</span>
					<p style="font-size: 20px;">联系电话:{{ user[0].account }}</p>
					<div class="bottom clearfix">
						<span style="font-size: 20px;">个人特长：</span>
					  <time class="time">{{ user[0].speciality }}</time>
					</div>
		      </div>
		      
		    </el-card>
			
		<!-- 编辑用户的对话框 -->
		<el-dialog
		  title="编辑用户"
		  :visible.sync="updateDialogVisible"
		  width="50%"
		  >
		<el-form :model=" updateForm"  label-width="70px">
			
			<el-form-item label="姓名">
				<el-input v-model="updateForm.name" :disabled="true"></el-input>
			</el-form-item>
			<el-form-item label="手机号" >
				<el-input v-model="updateForm.account" :disabled="true"></el-input>
			</el-form-item>
			<el-form-item label="身份" >
				<el-input v-model="updateForm.type" :disabled="true"></el-input>
			</el-form-item>
		    <el-form-item label="重置密码">
		    	<el-input v-model="updateForm.repassword" ></el-input>
		    </el-form-item>
		    <el-form-item label="特长">
		    	<el-input v-model="updateForm.speciality" ></el-input>
		    </el-form-item>
		</el-form>
		  <!-- 底部区域 -->
		  <span slot="footer" class="dialog-footer">
			<el-button type="primary" @click="unadteUser()">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import store from '../../store.js'
export default{
	store,
	computed:{
		account:function(){
			return this.$store.state.account
		}
	},
	created() {
		this.getuser()
	},
	 data() {
		return {
			user:[],
			account:this.$store.state.account,
			updateDialogVisible:false,
			updateForm:{
				account:"",
				name:"",
				type:"",
				repassword:"",
				speciality:""
			}
				
				
			
		}
	},
	methods:{
		getuser(){
			const that = this
			 this.axios.get('http://localhost:3000/users/getUsersByid',{
				 params:{
					account:that.account
				 }
			})
			  .then(function (res) {
				  console.log(res)
				  that.user = res.data.message
				  that.updateForm = res.data.message[0]
			  })
			  .catch(function (error) {
				console.log(error);
			  });
		},
		unadteUser(){
			const that = this
				this.axios.put('http://localhost:3000/users/update/' + that.account,{
				speciality:that.updateForm.speciality,
			})
			  .then(function (res) {
				  console.log(res)
				  that.updateDialogVisible = false
				  that.$message({
				            message: '修改成功',
				            type: 'success'
				          });
			  })
			  .catch(function (error) {
			    console.log(error);
			  });
		},
		look(){
			console.log(this.user);
		}
	}
  
}
</script>

<style lang = "less" scoped>
	.all{
		/* background-color: #000000; */
		padding-left: 60px;
		padding-right: 60px;
		padding-top: 20px;
	}

  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
	margin-left: 15px;
  }

  .image {
    width: 200px;
	height: 200px;
	border-radius: 50%;
	margin-top: 20px;
	margin-bottom: 40px;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>

