import Vue from 'vue';
import Router from 'vue-router';
import Login from './components/Login.vue';
import Register from './components/register.vue';
import Register2 from './components/register2.vue';
import Home from './components/commonuser/home.vue';
import Welcome from './components/commonuser/welcome.vue'
import Commonuser from './components/commonuser/Commonuser.vue'
import Wushu from './components/commonuser/findwushu.vue'
import Zhuangxiu from './components/commonuser/findzhuangxiu.vue'
import Diangong from './components/commonuser/finddiangong.vue'
import Attention from './components/commonuser/attention.vue'
import Mymessage from './components/commonuser/message.vue'
import store from './store.js'
Vue.use(Router);

const router = new Router({
    routes:[
        { path: '/', redirect: '/Login'},
        { path: '/Login',component: Login,
        	redirect:'/register',
        		children:[
        			{ path: '/register',component: Register },
        			{ path: '/register2',component: Register2 }
        ]},
		{
			path: '/commonuser/home',
			component: Home, 
			redirect:'/welcome',	
			children:[
				{ path: '/welcome',component: Welcome },
				{ path: '/commonuser',component: Commonuser },
				{ path: '/wushu',component: Wushu },
				{ path: '/zhuangxiu',component: Zhuangxiu },
				{ path: '/diangong',component: Diangong },
				{ path: '/attention',component: Attention },
				{ path: '/message',component: Mymessage }
		]},
    ]
})


//挂载路由守卫
//来判断用户是否登录过
router.beforeEach((to,from,next) => {
	//如果用户访问的是登录页，直接放行
	if(to.path === '/register')	return next()
	//从sessionStorage中获取到保存的token值
	const tokenStr = store.state.account
	// const tokenStr  = window.sessionStorage.getItem('token')
	//没有token, 强制跳转到登录页
	if(!tokenStr) return next('/register')
	next()
})
export default router