<template>
	<div class="big-contain">
	<el-form  label-width="100px" hide-required-asterisk :inline="true">
	  <el-form-item label="手机号">
	    <el-input  prefix-icon="iconfont icon-android " v-model="upFrom.phone" ></el-input>
	  </el-form-item>
	  <el-button @click = "send_code" :disabled = 'isdisabled'>发送验证码</el-button>
	  <el-form-item label="验证码">
	    <el-input v-model="code"></el-input>
	  </el-form-item>
	</el-form>
	<el-row>	
		<button class="sbutton2" @click="next">下一步</button>
	</el-row>	
	</div>
</template>

<script>
	import store from '../store.js'
	export default{
		store,
		data(){
			return{
				code:'',//输入的验证码
				codenum:'',//服务器发送的验证码
				isdisabled:false,
				upFrom:{
					phone:'',
					isregister:false,//若验证码匹配成功，切换路由
				}
			}
		},
		methods:{
			send_code(){
				if(this.upFrom.phone.length === 11){
					const that = this
					this.isdisabled = true
					this.$message({
					          message: '验证码发送成功',
					          type: 'success'
					        });
					this.axios.post('http://localhost:3000/users/sendCorecode',null,{
						params:{
							// params就是query参
							phone:that.upFrom.phone,
						}
					})
					  .then(function (res) {
						  console.log(res)
						  if(res.data.code !== 200){
							  that.$message.error('请确认您的手机号或密码是否正确');
						  }else{
							  that.codenum = res.data.codenum
						  }
					  })
					  .catch(function (error) {
					    console.log(error);
					  });
				}else{
					this.$message.error('请确认您的手机号或密码是否正确');
				}
			},
			next(){
				const that = this
				if(this.code == '')	{
					this.$message.error('请输入验证码');
				}else{
					console.log(that.codenum)
					console.log(that.code)
					if(parseInt(that.code) === that.codenum){
						that.$emit("phone-register",that.upFrom)
						// 把当前登录的用户phone传到store
						that.$store.commit('myinfo', that.upFrom.phone)
					}else{
						this.$message.error('验证码错误');
					}
				}
			}
			

		}
	}
</script>

<style scoped>

	.big-contain{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color:#dff9fb;
	}
	.sbutton2{
		width: 60%;
		height: 40px;
		border-radius: 24px;
		border: 1px solid ##c6e6e8;
		outline: none;
		background-color: transparent;
		color: ##c6e6e8;
		font-size: 0.9em;
		cursor: pointer;
		margin-left: 90px;
		margin-top: 10px;
		width: 200px;
	}
</style>
