<template>
	<div class="login-register">
		<div class="head_font">
			<h1>HC安全找师傅</h1> 
		</div>
		<div class="contain">
			<!-- 主体内容 -->
			<div class="big-box" :class="{active:isLogin}">
				<!-- 登录 -->
				<div class="big-contain" v-if="isLogin">
					<el-form :model="userFrom" :rules="rules" ref="userFrom" label-width="100px" hide-required-asterisk>
					  <el-form-item label="手机号" prop="phone">
					    <el-input v-model="userFrom.phone" prefix-icon="iconfont icon-android "></el-input>
					  </el-form-item>
					  <el-form-item label="密码">
					    <el-input v-model="userFrom.password" prefix-icon="iconfont icon-gongkai" type="password"  @keyup.enter.native="login"></el-input>
					  </el-form-item>
					</el-form>
					<el-row>
						<button class="sbutton2" @click="login">登录</button><!-- 
						<button class="sbutton2" @click="Test">Test</button>
						<button class="sbutton2" @click="Test2">Test2</button> -->
					</el-row>
				</div>
				<!-- 注册 -->
				<div class="big-contain" v-else>
					<div  v-if="isregister">
						<register @phone-register = "showmsg"></register>
					</div>
					<div v-else>
						<register2/>
					</div>
				</div>
			</div>
			<!-- 侧面词内容 -->
			<div class="small-box" :class="{active:isLogin}">
				<div class="small-contain" v-if="isLogin">
					<div class="stitle">你好，朋友!</div>
					<p class="scontent">在这里注册，这里应有尽有</p>
					<button class="sbutton" @click="changeType">注册</button>
				</div>
				<div class="small-contain" v-else>
					<div class="stitle">欢迎回来!</div>
					<p class="scontent">与我们保持联系，请登录你的账户</p>
					<button class="sbutton" @click="changeType">登录</button>
				</div>
			</div>
		</div>
		
	</div>
</template>

<script>
	import register2 from './register2.vue'
	import register from './register.vue'
	import store from '../store.js'
	export default{
		store,
		components:{
			register,register2
		},
		data(){
			return {
				phonenum:'',
				isLogin:true,
				isregister:true,
				userFrom: {
				          phone: '',
				          password: '',
				        },
				 rules: {
						  phone: [
							{ required: true, message: '请输入手机号', trigger: 'blur' },
							{ min: 11, max: 11, message: '手机号应为11位', trigger: 'blur' }
						  ],
						}
			}
		},
		methods:{
			changeType() {
				this.isLogin = !this.isLogin
			},

			login() {
				const that = this
				if(this.userFrom.password === ''){
					this.$message.error("请输入密码")
				}else{
				this.axios.post('http://localhost:3000/users/login',null,{
					params:{
						// params就是query参
						account:that.userFrom.phone,
						password:that.userFrom.password
					}
				})
					.then(function (res) {
					console.log(res)
					if(res.data.err_code === 1){
						alert("该用户不存在，登陆失败")
					}else{
						that.$store.commit('myinfo', that.userFrom.phone)
						that.$router.push('/commonuser/home')
						window.sessionStorage.setItem("token",res.data.token)
					}
				})
					.catch(function (error) {
					console.log(error);
				});
				}
			},
			register(){
			
			},
			register_next(){
				this.$router.push('/commonuser/home')
			},
			showmsg(data){
				this.isregister = data.isregister
			}
		}
	}
</script>

