<template>
	<!-- 注册成功并设置完密码后弹出欢迎框 -->
	<div class="big-contain">
		<el-dialog
		  title="注册成功"
		  :visible.sync="dialogVisible"
		  width="40%"
		  append-to-body>
		  <span>Welcome to HC安全找师傅！</span>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="goto">o k</el-button>
		  </span>
		</el-dialog>


	<el-form  label-width="100px" hide-required-asterisk :rules="rules" ref="addfome">
	  <el-form-item label="设置您的姓名" prop="name">
	    <el-input  prefix-icon="iconfont icon-gongkai" v-model="addfome.name"></el-input>
	  </el-form-item>

	  <el-form-item label="设置您的密码" prop="password">
	    <el-input  prefix-icon="iconfont icon-gongkai" v-model="addfome.password"></el-input>
	  </el-form-item>
	  
	  <el-form-item label="选择您的身份">
	    <el-select v-model="value" placeholder="请选择">
	        <el-option
	          v-for="item in options"
	          :key="item.value"
	          :label="item.label"
	          :value="item.value">
	        </el-option>
	      </el-select>
	  </el-form-item>
	</el-form>
	<el-row>
		<button class="sbutton" @click="next">确定</button>
	</el-row>
	
	</div>
</template>

<script>
import store from '../store.js'
	export default{
		store,
		computed:{
			account:function(){
				return this.$store.state.account
			}
		},
		data(){
			return{
				addfome:{
					account:this.$store.state.account,
					password:'',
					name:'',
				},
				dialogVisible: false,
				 options: [{
				          value: '1',
				          label: '普通人'
				        }, {
				          value: '2',
				          label: '武术师傅'
				        }, {
				          value: '3',
				          label: '装修师傅'
				        }, {
				          value: '4',
				          label: '电工师傅'
				        }],
				value: '',
				rules: {
					  name: [
						{ required: true, message: '姓名不能为空', trigger: 'blur' }
					  ],
					  password: [
						{ required: true, message: '密码不能为空', trigger: 'blur' }
					  ]
				}
			}
		},
		methods:{
			next(){
					const that = this
					if(this.value === ''){
						this.$message.error("请选择您的身份类型")
					}else{
						this.axios.post('http://localhost:3000/users/adduser',{
							account:that.addfome.account,
							password:that.addfome.password,
							name:that.addfome.name,
							type:that.value
						})
						.then(function (res) {
						  that.dialogVisible = true
						  console.log(res)
						})
						.catch(function (error) {
						console.log(error);
						});
					}
					
			},
			goto(){
				this.dialogVisible = false
				this.$router.push('./commonuser/home')
			}
		}
	}
</script>

<style scoped>
	.sbutton{
		width: 60%;
		height: 40px;
		border-radius: 24px;
		border: 1px solid ##c6e6e8;
		outline: none;
		background-color: transparent;
		color: ##c6e6e8;
		font-size: 0.9em;
		cursor: pointer;
		margin-left: 90px;
		margin-top: 10px;
		width: 200px;
		margin-bottom: 10px;

	}
</style>
