</template>

<script>
	import store from '../../store.js'
export default{
	store,
	computed:{
		account:function(){
			return this.$store.state.account
		}
	},
	created() {
		this.getAttention()
		
	},
	 data() {
		return {
		  account:this.$store.state.account,
		  attentionsList:[],
		}
	},
	methods:{
		getAttention(){
			const that = this
			 this.axios.get('http://localhost:3000/attention',{
				 params:{
					 fansid:that.account
				 }
			})
			  .then(function (res) {
				  console.log(res)
				  that.attentionsList = res.data.message
			  })
			  .catch(function (error) {
				console.log(error);
			  });
		},
		getOne(){
			const that = this
			 this.axios.get('http://localhost:3000/users/getUsersByid',{
				 params:{
					 account:that.attentionsList[0].starid
				 }
			})
			  .then(function (res) {
				  console.log(res)
				  
			  })
			  .catch(function (error) {
				console.log(error);
			  });
		},
		look(){
			console.log(this.account);
			console.log(this.attention);
		},
			
		attent(){
			console.log(this.account);
			console.log(this.attention);
			this.attentstatu = !this.attentstatu
			if(this.attentstatu !== true)
			this.$message.success('关注成功')
			else 
			this.$message.success('取关成功')
		},
		goBack(){
			this.$router.push('./commonuser/home')
		}
	}
  
}
</script>

